.App {
  
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

body {
  background-color: #2C2B35;
}

.main-container {
  width: 100%;
}

.main-display {
  display: grid;
  gap: 20px;
  width: 250px;
  height: 200px;
  border: 2px solid #70E4FB;
  border-radius: 10px;
  margin: 0 auto;
  margin-top: 150px;
  padding: 20px;
  
}

.counter-display {
  font-size: 5rem;
  font-weight: 700;
  grid-column: 1/12;
  grid-row: 1/3;
  display: grid;
  align-items: center;
  background-color: #70E4FB;
  border-radius: 10px;
}

.button-display {
  grid-column: 1/12;
  grid-row: 3/5;
  box-sizing: border-box;
  border-radius: 10px;
  border: 2px solid #70E4FB;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: repeat(2, minmax(1px, 1fr)); 
 
}

.button-display button {
  display: inline;
  font-size: 1.5rem;
  font-weight: 700;
  background-color: #70E4FB;
  width: fit-content;
  height: fit-content;
  padding: 8px;
  border-radius: 10px;
}

.button-display button:disabled {
  background-color: #70e4fbaa;
  color: black;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.counter-limit {
  color: red
}